.styledButton {
  color: inherit;
  background-color: transparent;
  border: 1px solid currentColor;
  padding: 0.35em 1.5em;
  border-radius: 0.9em;
  cursor: pointer;
  font: inherit;
  display: block;
  text-transform: capitalize;
  user-select: none;
  white-space: nowrap;
  transition: background-color 250ms ease-in-out;
}
.styledButton.active, .styledButton.prev {
  color: #3abdd9;
}
.styledButton.active {
  cursor: default;
  background-color: #e2e2e2;
}
.styledButton:not(.active, .buttonSubmit):hover {
  background-color: #e2e2e2;
}

.nav {
  grid-area: nav;
}

.navList {
  display: flex;
  flex-direction: column;
  height: 100%;
  counter-reset: section;
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 52em) {
  .navList {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    max-width: 50em;
  }
}

.navItem {
  margin: 0 0 1em;
}
@media (min-width: 52em) {
  .navItem {
    flex: 1;
    margin: 0 0.3em 0.3em;
  }
}

.navSection {
  composes: navItem;
}

.navItemButton {
  width: 100%;
  counter-increment: section;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0.5em;
  text-align: left;
}
.navItemButton:before {
  content: counter(section);
  font-size: 0.8em;
  height: 1.4em;
  width: 1.4em;
  background-color: #0d131e;
  color: #fff;
  text-align: center;
  line-height: 1.4em;
  border-radius: 50%;
  margin-right: 0.5em;
  font-weight: bold;
}
.navItemButton.active {
  background-color: #3abdd9;
  border-color: #3abdd9;
  color: #fbfbfb;
}

.navSectionButton {
  composes: styledButton;
  width: 100%;
  font-weight: bold;
}