.styledButton {
  color: inherit;
  background-color: transparent;
  border: 1px solid currentColor;
  padding: 0.35em 1.5em;
  border-radius: 0.9em;
  cursor: pointer;
  font: inherit;
  display: block;
  text-transform: capitalize;
  user-select: none;
  white-space: nowrap;
  transition: background-color 250ms ease-in-out;
}
.styledButton.active, .styledButton.prev {
  color: #3abdd9;
}
.styledButton.active {
  cursor: default;
  background-color: #e2e2e2;
}
.styledButton:not(.active, .buttonSubmit):hover {
  background-color: #e2e2e2;
}

.form {
  display: contents;
}

.main {
  grid-area: main;
}

.footer {
  grid-area: footer;
}

.main {
  padding: 2em min(1.6rem, 3vw);
  text-align: initial;
  width: min(100%, 50em);
  border: 1px solid #ccc;
  margin: 0 auto;
  border-radius: 1em;
}
@media (min-width: 52em) {
  .main {
    margin: 1.5em auto;
  }
}

.question {
  margin-top: 0;
  font-size: 1.5em;
}

.formGroup {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.radioLabel {
  flex: 1;
  margin: 0 0.3em 0.3em;
}
.radioLabel input[type=radio] {
  display: none;
}
.radioLabel span {
  display: block;
  font-size: 1.2em;
  padding: 0 1.2em;
  line-height: 2.1;
  border: 1px solid #afafaf;
  border-radius: 0.5em;
  cursor: pointer;
  text-align: center;
  user-select: none;
}
.radioLabel input[type=radio]:checked + span {
  border-color: #3abdd9;
}

.controlInput {
  display: block;
  width: 100%;
  font-size: 1.2em;
  line-height: 2;
  padding-left: 1.2em;
  border: 1px solid #afafaf;
  border-radius: 0.5em;
  margin-bottom: 0.3em;
}
.controlInput:hover, .controlInput:focus {
  outline-width: 0.1rem;
  outline-color: #3abdd9;
}

.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.footer span {
  margin-right: 0.6em;
  display: flex;
  align-items: center;
  white-space: nowrap;
}
.footer b {
  margin-left: 0.3em;
}
.footer svg {
  margin-right: 0.3em;
}

.footerButton {
  color: inherit;
  background-color: transparent;
  border: 1px solid currentColor;
  padding: 0.35em 1.5em;
  border-radius: 0.9em;
  cursor: pointer;
  font: inherit;
  display: block;
  text-transform: capitalize;
  user-select: none;
  white-space: nowrap;
  transition: background-color 250ms ease-in-out;
  font-size: 1.4em;
  border-radius: 0.2em;
  padding: 0.4em 2em;
  margin: 0.3em;
}
.footerButton:hover {
  background-color: #e2e2e2;
}

.buttonSubmit {
  composes: footerButton;
  background-color: #3abdd9;
  border-color: #3abdd9;
  color: #fbfbfb;
}
.buttonSubmit:hover {
  background-color: #24a1bc;
}