.styledButton {
  color: inherit;
  background-color: transparent;
  border: 1px solid currentColor;
  padding: 0.35em 1.5em;
  border-radius: 0.9em;
  cursor: pointer;
  font: inherit;
  display: block;
  text-transform: capitalize;
  user-select: none;
  white-space: nowrap;
  transition: background-color 250ms ease-in-out;
}
.styledButton.active, .styledButton.prev {
  color: #3abdd9;
}
.styledButton.active {
  cursor: default;
  background-color: #e2e2e2;
}
.styledButton:not(.active, .buttonSubmit):hover {
  background-color: #e2e2e2;
}

.container {
  padding: 1.6rem;
  text-align: center;
  display: grid;
  grid-template-areas: "header header" "nav main" "nav footer";
  grid-template-columns: min-content auto;
  grid-gap: 1.6rem;
}
@media (min-width: 52em) {
  .container {
    display: block;
  }
}

.header {
  grid-area: header;
}

.title {
  font-size: 2em;
}